
 h2{
     color: #0099ff;
     font-size: 24px;
}
 .btn-get-started{
          font-weight: 500;
          font-size: 16px;
          letter-spacing: 1px;
          display: inline-block;
          padding:  10px 30px;
          border-radius: 50px;
          border:  2px solid #3498db;
          text-decoration: none;
          transition: 0.5s;

}

.btn-get-started:hover{
     background-color: #3498db;
     color: #fff;
}

img{
     text-align: right;
     overflow: hidden;
          user-select: none;
          position: relative;
        
}

.cards{
     box-shadow: 4px 4px 8px 0 rgba(0,0,0,0.2);
     border-radius:20px;
     transition:0.3s
}

.cards:hover{
     transition:0.3s;
     box-shadow: 8px 8px 16px 0 rgba(0,0,0,0.2),-8px -8px 16px 0 rgba(0,0,0,0.2);
}

.animated{
     animation: up-down 2s ease-in-out infinite alternate-reverse;
}

@keyframes up-down{
     0%{
          transform: translateY(10px);
     }
     100%{
          transform: translateY(-10px);
     }

     
}

@-webkit-keyframes up-down{
     0%{
          transform: translateY(10px);
     }
     100%{
          transform: translateY(-10px);
     }

     
}

