
.header {
     background-color: #0099ff;
     box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);
     position: fixed;
     width: 100%;
     z-index: 3;
     top: 0;
   }
   
   .header ul {
     margin: 0;
     padding: 0;
     list-style: none;
     overflow: hidden;
     background-color: #0099ff;
   }

  
   
   .header li a {
     display: block;
     padding: 10px 20px;
     color: #fff;
     font-size: large;
     text-decoration: none;
     border-radius: 15px;
   }
   
   .header li a:hover,
   .header .menu-btn:hover {
     text-shadow: 0 0 5px #ff0000;
   }

   .menu_active{
     background-color: #FF3C3C;
     font-size: 20px;
   }
   
   .header .logo {
     float: left;
     padding: 10px 20px;
     text-decoration: none;

   }
   .header .logo img{
     width: 250px;

   }
   
   /* menu */
   
   .header .menu {
     clear: both;
     max-height: 0;
     margin-top: 20px;
     margin-right: 10px;
     transition: max-height .2s ease-out;
   }
   
   /* menu icon */
   
   .header .menu-icon {
     margin-top: 40px;
     cursor: pointer;
     float:right;
     margin-right: 15px;
     padding: 28px 20px;
     position: relative;
     user-select: none;
   }
   
   .header .menu-icon .navicon {
     background: #333;
     display: block;
     height: 2px;
     position: relative;
     transition: background .2s ease-out;
     width: 18px;
   }
   
   .header .menu-icon .navicon:before,
   .header .menu-icon .navicon:after {
     background: #333;
     content: '';
     display: block;
     height: 100%;
     position: absolute;
     transition: all .2s ease-out;
     width: 100%;
   }
   
   .header .menu-icon .navicon:before {
     top: 5px;
   }
   
   .header .menu-icon .navicon:after {
     top: -5px;
   }
   
   /* menu btn */
   
   .header .menu-btn {
     display: none;
   }
   
   .header .menu-btn:checked ~ .menu {
     max-height: 240px;
   }
   
   .header .menu-btn:checked ~ .menu-icon .navicon {
     background: transparent;
   }
   
   .header .menu-btn:checked ~ .menu-icon .navicon:before {
     transform: rotate(-45deg);
   }
   
   .header .menu-btn:checked ~ .menu-icon .navicon:after {
     transform: rotate(45deg);
   }
   
   .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
   .header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
     top: 0;
   }
   
   /* 48em = 768px */
   
   @media (min-width: 60em) {
     .header li {
       float: left;
     }
     .header li a {
       padding: 20px 30px;
     }
     .header .menu {
       clear: none;
       float: right;
       max-height: none;
     }
     .header .menu-icon {
       display: none;
     }
     .header .logo img{
          width: 350px;
          margin-left: 50px;
        }
        
   }

   @media (max-width: 475px){
     .header .logo img{
          width: 200px;
          margin-left: 2px;
        }
        .header .menu-icon {
             margin-top: 20px;
        }
   }
   